import React, { Component } from "react";
import { fetchPage } from "./lib/api";

export default class extends Component {
  constructor() {
    super();
    this.fetchPage = this.fetchPage.bind(this);

    this.state = {
      loading: false,
      content: undefined
    };
  }

  componentDidMount() {
    const { match: { params: { title } } } = this.props;

    return this.fetchPage(title);
  }

  fetchPage(title) {
    this.setState({ loading: true });

    return fetchPage(decodeURIComponent(title)).then(({ parse, error }) => {
      if (error) {
        this.setState({
          loading: false,
          error: error.info
        });
      } else {
        this.setState({
          loading: false,
          title: parse.title,
          content: parse.text["*"]
        });
      }
    });
  }

  render() {
    const { title, location: { search } } = this.props;
    const { loading, content, error } = this.state;

    return (
      <div>
        {loading && <p>Loading&hellip;</p>}
        {content ? (
          <div>
            <h1>{this.state.title}</h1>
            <div dangerouslySetInnerHTML={{ __html: content }} />
          </div>
        ) : error ? (
          <div>
            <h1>Error</h1>
            <div>{error}</div>
          </div>
        ) : (
          <h1>{title}</h1>
        )}
      </div>
    );
  }
}



